import { defHttp } from '/@/utils/http/axios';
import { BasicColumn, FormProps } from '/@/components/Table';

enum Api {
  createOrgTypeUrl = '/system/org-type/create',
  updateOrgTypeUrl = '/system/org-type/update',
  deleteOrgTypeUrl = '/system/org-type/delete',
  updateSortUrl = '/system/org-type/update-sort',

  getOrgTypeListUrl = '/system/org-type/page',
}
// 新增
export const createOrgType = (params) => defHttp.post({ url: Api.createOrgTypeUrl, params }, {});
//修改
export const updateOrgType = (params) => defHttp.post({ url: Api.updateOrgTypeUrl, params }, {});
//保存
export const saveOrgType = (params) => {
  if (!params.id) {
    return createOrgType(params);
  }
  return updateOrgType(params);
};
//删除
export const deleteOrgType = (id) =>
  defHttp.delete({ url: Api.deleteOrgTypeUrl + '?id=' + id }, {});

//更新排序号
export const updateSort = (params) => defHttp.post({ url: Api.updateSortUrl, params }, {});
//查看列表
export const getOrgTypeList = (params) =>
  defHttp.get({ url: Api.getOrgTypeListUrl, params }, { isOnlyResult: true });

//列表搜索条件
export const searchFormSchema: Partial<FormProps> = {
  labelWidth: 120,
  actionColOptions: {
    span: 24,
  },
  autoAdvancedLine: 1,
  showAdvancedButton: true,
  baseColProps: {
    span: 7,
  },

  schemas: [
    { label: '编码', field: 'code', component: 'Input' },
    { label: '名称', field: 'name', component: 'Input' },
  ],
};

//主页面列表字段展示
export const columns: BasicColumn[] = [
  { title: '编码', dataIndex: 'code', width: 150, resizable: true, align: 'center' },
  { title: '名称', dataIndex: 'name', width: 150, resizable: true, align: 'center' },
  { title: '排序号', dataIndex: 'sort', width: 80, resizable: true, align: 'center' },
  { title: '状态', dataIndex: 'status', width: 100, resizable: true, align: 'center' },
];
